import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniPagination from "@/components/uni-pagination/uni-pagination.vue"

let that;
let day;
let uid;
let page_size = 50;
export default {
	components: {
		loading,
		faIcon,
		uniPagination,
		uniSegmentedControl
	},
	data() {
		return {
			page_num: 1,
			logsList: "",
			loading: true,
			items: ['今天', '一个月', '一年'],
			current: 0
		}
	},
	onLoad() {
		day = 1;
		that = this;
		uid = uni.getStorageSync("uid");
		that.get_logsList(0);
	},
	methods: {
		
		//分页点击
		change_page(e) {
			that.page_num = e.current
			that.get_logsList(day, true);
		},
		
		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index

				if (index == 0) {
					day = 0;
					that.get_logsList(0)
					console.log(common.getDay(1, true))
				} else if (index == 1) {
					day = -30;
					that.get_logsList(-30)
					console.log(common.getDay(-30, true))
				} else {
					day = -365;
					that.get_logsList(-365)

					console.log(common.getDay(-365, true))
				}
			}
		},

		//得到日志列表
		get_logsList(day) {
			const query = Bmob.Query("logs");
			query.equalTo("parent", "==", uid);
			query.equalTo("type", "==", -2);
			query.equalTo("createdAt", ">=", common.getDay(day, true));
			query.order("-createdAt")
			query.limit(page_size);
			query.skip(page_size * (that.page_num - 1));
			query.find().then(res => {
				//console.log(res)
				that.logsList = res
				that.loading = false;
			});
		},

	}
}
